@import './Blog.scss';
@import '../../views/mixins.scss';

.BlogList {
    background-color: #303136;
    .hero-blog {
        @include tablet {
            padding: 0px 10px;
        }
        img {
            width: 100%;
            height: 100%;
            border-radius: 10px;
        }
    }
    .blog-list-container {
        padding: 50px 20px;
    }
    .metadata {
        margin: 0px;
        vertical-align: middle;
        @include tablet {
            margin-top: 20px;
            h3 {
                font-size: 1.5em;
                margin: 14px 0px;
            }
        }
    }
    p {
        font-size: 1.2rem;
        @include tablet {
            line-height: 1.5em;
        }
    }
    .vert-middle-container {
        margin-top: auto;
        margin-bottom: auto;
    }
    .img-block {
        display: inline-flex;
    }
    .blog-item {
        @include tablet {
            margin-top: 60px;
        }
        img {
            width: 100%;
            border-radius: 3px;
        } 
        .metadata {
            margin-top: 20px;
            .category {
                font-size: 0.8em;
            }
            h4 {
                margin: 10px 0px;
                line-height: 1.3em;
            }
            p.desc {
                font-size: 1.3em;
                line-height: 1.4em;
            }
        }
    }
    .with-top-margin {
        padding: 50px 0px;
        margin: 50px 0px;
        border-top: 1px solid #404148;
        @include tablet {
            padding: 0px;
        }
    }
}