.blog-content {
    margin: auto auto;

    a {
        color: #fff;
    }
    a:hover {
        color: #6097fe;
    }
    
    blockquote {
        background-color: #00000012;
        padding: 10px;
        border-right: 2px solid #000;
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
        * {
            margin: 0px;
        }
    }
    
    table {
        width: 100%;
        border-collapse: separate;
        border-spacing: 0 15px; 
        margin-bottom: 20px;
        th, td {
            text-align: center;
        }
        th {
            font-size: 1.2em;
            padding-bottom: 7px;
        }
        tr {
            padding-bottom: 20px;
        }
        td:nth-child(1) {
            padding: 0px 10px;
        }
    }

    code {
        background-color: #4aac6c;
        box-shadow: 5px 0 0 #4aac6c, -5px 0 0 #4aac6c;
        color: #fff;
        font-family: 'quicksand-light';
        font-weight: 600;
        padding: 3px 0px;
        border-radius: 5px;
    }

    hr {
        margin: 30px 0px 50px 0;
    }

    .subheading {
        h3 {
            margin-top: 30px;
            font-size: 1.9em;
        }
    }

    img {
        margin: auto auto;
        width:  100%;
    }

    figure.img-full-width {
        width: 100vw;
        position: relative;
        left: 50%;  
        margin-left: -50vw;
        p, img {
            width: 100%;
            margin-bottom: 20px;
        }
    }
}

