.Author {
    border-top: 1px solid #585a63;
    margin-top: 20px;
    padding-top: 15px;
    div {
        display: inline-block;
    }
    .author-img {
        width: 40px;
        img {
            width: 100%;
        }
        vertical-align: top;
    }
    .author-name-creation {
        margin-left: 20px;
        padding-top: 5px;
        .creation-date {
            font-size: 0.8em;
            font-family: 'Roboto';
            margin: 0;
        }
    }
    h5.author-name {
        padding-top: 3px;
        font-size: 0.8em;
        text-transform: uppercase;
        margin: 0;
    }
}