@import '../../views/mixins.scss';

.Blog {
    background-color: #303136;
    h1, h2, h3, h4, h5 {
        color: #fff;
        border: none;
        padding: 0;
        font-family: 'Roboto';
        font-weight: 500;
        text-rendering: optimizeLegibility;
        -webkit-font-smoothing: antialiased;
        line-height: 1.2em;
    }
    img {
        border-radius: 3px;
    }
    .blog-content {
        h1,  h2, h3, h4, h5, h6 {
            margin-top: 50px;
        }
        h4 {
            font-size: 2em;
        }
    }
    p {
        color: #ddd;
        font-family: Georgia,serif;
        font-size: 1.4rem;
        line-height: 1.7em;
    }
    .metadata {
        margin: 60px 0px;
        .category {
            font-size: 0.9em;
            font-weight: 700;
            color: #6097fe;
        }
        h2 {
            font-size: 3.5rem;
            @include tablet {
                font-size: 2.5rem;
                margin: 9px 0px;
            }
        }
    }
    .content {
        padding: 0px 24%;
        color: #fff;
        padding-bottom: 40px;
        @include tablet {
            padding: 0px 10%;
        }
    }
    .loading-content {
        height: 900px;
    }
}